<template>
<div class="app-order ">
    <!--  -->
  <mt-header title="">
  <router-link :to="'/self'" slot="left">
    <mt-button icon="back"></mt-button>
  </router-link>
</mt-header>
    <!--  -->
<mt-navbar v-model="selected">
  <mt-tab-item id="1"></mt-tab-item>
  <mt-tab-item id="2"></mt-tab-item>
  <mt-tab-item id="3"></mt-tab-item>
  <mt-tab-item id="4"></mt-tab-item>
</mt-navbar>
<!-- tab-container -->
<mt-tab-container v-model="selected">
    <!--  -->
  <mt-tab-container-item id="1">
    <div class="topay">
        <img src="../img/wait.png">
       <div> </div>
    </div>
  </mt-tab-container-item>
  <!--  -->
  <mt-tab-container-item id="2">
<div class="mui-card">
    <div class="mui-card-content">
        <div class="mui-card-content-inner">
            <img src="../img/wait2.png">
            <div>
            <p></p>
            <p class='mui-ellipsis'>,</p></div>
        </div>
    </div>
</div>
   <ul class="mui-table-view">
    <li class="mui-table-view-cell mui-media" v-for="(item,i) of shoplist" :key=i>
        <img class="mui-media-object mui-pull-left" :src="''+item.pic">
        <div class="mui-media-body">
            {{item.pname}}
            <p class='mui-ellipsis'>{{item.price}}</p>
           <button class="btn" @click="remind"></button>
        </div>
    </li>
</ul>
  </mt-tab-container-item>
  <!--  -->
  <mt-tab-container-item id="3">
    <ul class="mui-table-view">
    <li class="mui-table-view-cell mui-media" v-for="(item,i) of shoplist" :key=i>
        <img class="mui-media-object mui-pull-left" :src="''+item.pic">
        <div class="mui-media-body">
            {{item.pname}}
            <p class='mui-ellipsis'>{{item.price}}</p>
        </div>
        <div class="sum">
            1 :{{item.price.toFixed(2)}}(10)
        </div>
        <button class="btn" @click="remind"></button>
    </li>
</ul>
  </mt-tab-container-item>
  <!--  -->
  <mt-tab-container-item id="4">
      <ul class="mui-table-view">
    <li class="mui-table-view-cell mui-media" v-for="(item,i) of shoplist" :key=i>
        <img class="mui-media-object mui-pull-left" :src="''+item.pic">
        <div class="mui-media-body">
            {{item.pname}}
            <p class='mui-ellipsis'>{{item.price}}</p>
           <button class="btn" @click="refund"></button>
        </div>
    </li>
</ul>
  </mt-tab-container-item>
</mt-tab-container>
</div>
</template>
<script>
import {Toast} from "mint-ui"
export default {
    data(){
        return{
            selected:'1',    //
            uid:1,
            shoplist:[]
        }
    },
    created(){
        this.loadMore()
    },
    methods:{
        //
        loadMore(){
            var url="shop?uid="+this.uid;
            this.axios.get(url).then(result=>{
                // console.log(result.data.data);
                this.shoplist=result.data.data;
                console.log(this.shoplist);
            })
        },
        //
        remind(){
             Toast("...");
        },
        //
        refund(){
            Toast(",24")
        }
    }
}
</script>
<style scoped>
    /*  */
.mint-navbar .mint-tab-item.is-selected{
    color: #bed5ab;
    border-bottom:3px solid  #bed5ab;
}
/**/
.mint-header{
background: rgba(0, 0, 0, 0.1);
    color: #fff;
}
    /*  */
a{color: #000;}
    /*  */
.topay{
    display: block;
    text-align: center;
    margin-top: 110px;
}
    /*  */
.mui-table-view{
    margin-top: 10px;
}
.mui-card-content-inner{
    display: flex;
    justify-content: space-around;
    align-items: center;
}
.mui-card-content-inner div p:nth-child(1){
    font-size: 16px;
    color: #000;
}
    /*  */
.btn{
    float: right;
    margin-top: 5px;
}
    /*  */
.sum{
    width: 100%;
    padding: 10px 0;
    padding-left:80px; 
    /* border-bottom: 1px solid #e8e8e8; */
    font-size: 16px;
    color: #949499;
}
</style>



