import Vue from 'vue'
import App from './App.vue'
import router from './router'
// ,
import add from './components/Add'

import'mint-ui/lib/style.css'
//7:main.js   qs
//axios  post  
import qs from 'qs'
Vue.prototype.qs=qs;
Vue.config.productionTip = false
//0.1:Vuex
import Vuex from "vuex"
//0.2Vuex
Vue.use(Vuex)
//0.3store
var store =new Vuex.Store({
  //
  state:{
    cartCount:sessionStorage.getItem("cartCount")||1
  },
  //
  mutations:{
    increment(state){
      state.cartCount++;
      sessionStorage.setItem("cartCount",state.cartCount);
    },
    reduce(state){
      state.cartCount--;
      sessionStorage.setItem("cartCount",state.cartCount);
    }
  },
  //
  getters:{
    optCartCount:function(state){
      return state.cartCount;
    }
  }
})
// console.log(store.getters.optCartCount)
//5:  
//Vue.http.options.root = "http://127.0.0.1/vue_ser/";
//6:post  application/x-www-form-urlencoded
//Vue.http.options.emulateJSON = true;
//  MUI   Bootstrap 
import './lib/mui/css/mui.css'

//  MUI 
// 
import './lib/mui/css/icons-extra.css'

//1:Header{}
import {Header,Button,Swipe,SwipeItem,TabContainer, TabContainerItem,InfiniteScroll,Cell,Navbar, TabItem} from "mint-ui"
//2:Header
Vue.component(Header.name,Header);
Vue.component(Button.name,Button);
Vue.component(Swipe.name,Swipe);
Vue.component(SwipeItem.name,SwipeItem);

//Navbar
Vue.component(TabContainer.name, TabContainer);
Vue.component(TabContainerItem.name, TabContainerItem);

//cell
Vue.component(Cell.name,Cell);
//Navbar
Vue.component(Navbar.name, Navbar);
Vue.component(TabItem.name, TabItem);

//  / 
Vue.component("my-add",add);

//3.main.js
import axios from "axios"
//4.main.jssession
axios.defaults.withCredentials=true;
//5.main.jsaxiosvue
Vue.prototype.axios=axios;


//6.main.js
//val 
Vue.filter("datatimeFilter",function(val){
  //6.1.
  var now=new Date(val);
  //6.2   
  var y = now.getFullYear();
  var m = now.getMonth()+1;
  var d = now.getDate();
  //6.3   
  var h =now.getHours();
  var mi =now.getMinutes();
  var s =now.getSeconds();
  //6.4  100
  m<10&&(m="0"+m);//if
  d<10&&(d="0"+d);
  //6.5 
  return `${y}-${m}-${d} ${h}:${mi}:${s}`;
});


new Vue({
  router,
  render: h => h(App),
  store     //0.4storeVue
}).$mount('#app')



