<template>
    <div class="app-login">
        <div id="title">
        <img src="../img/IMG_3779.png">
        <h1></h1>
        </div>
        <input type="text" name="uname" v-model="uname" placeholder="" class="inp">
            <br>
        <input type="password" name="upwd" v-model="upwd" placeholder="" class="inp">
            <br>
        <router-link :to="'/zhuce'" class="zhuce"></router-link>
            
    <mt-button @click="btnClick" size="large"></mt-button>
    </div>
</template>
<script>
import {Toast} from "mint-ui"
export default {
    data(){
        return {
            uname:"",
            upwd:""
        }
    },
    methods:{
        btnClick(){
            var uname=this.uname;
            var p=this.upwd;
            console.log(uname+":"+p);
            var reg=/^[a-z0-9_]{3,8}$/i;
            if(!reg.test(uname)){
                Toast(",")
                return;
            }
            if(!reg.test(p)){
                Toast(",")
                return;
            }
            var postData=this.qs.stringify({
                uname:uname,
                upwd:p
            })
            console.log(postData);
            //
            var url="";
            url+="/login";
            this.axios.post(url,postData).then(result=>{
                console.log(result.data)
                if(result.data.code==1){
                    Toast(",");
                    this.$router.push("/shopcart");
                }
            })
        }
    }
}
</script>
<style scoped>
    /*  */
#title{
    text-align: center;
    margin-bottom: 30px;
}
    /*  */
.inp{
    border:none;
    background: #efeff4;
    border-bottom: 1px solid #aaa9a9;
}
    /*  */
.mint-button--large{
    background: #bed5ab;
    color: #fff;
    margin-top: 5px;
}
.zhuce{
    padding-left: 85%;
    color: #000;
}
</style>



