<template>
<div class="app-goodinfo">
    <!--  -->
  <router-link :to="'/goodlist'" slot="left">
    <mt-button class="btn">
        <span class="mint-button-icon">
            <i class="mintui mintui-back"></i>
        </span> 
    </mt-button>
  </router-link>
  <!-- / -->
    <div class="mui-card">
         <div class="mui-card-header mui-card-media" >
            <mt-swipe :auto="4000" class="mt-swipe">
           <mt-swipe-item v-for="(item,i) of imgs" :key="i">
               <img :src="''+item.pic">
               </mt-swipe-item>
       </mt-swipe>
        </div> 
        <!-- \ -->
        <div class="mui-card-content">
            <div class="mui-card-content-inner">
                <p class="price"> <span>{{info.price}}</span> </p>
                <h4>{{info.title}}</h4>
                <p class="num">:999</p>
            </div>
        </div>
        <!--  -->
            <table>
                <thead>
                    <th colspan="2"></th>   
                </thead>
                <tbody>
                    <tr>
                        <td>:</td>
                        <td>{{info.subtitle}}</td>
                    </tr>
                    <tr>
                        <td>:</td>
                        <td>{{info.spe}}</td>
                    </tr>
                    <tr>
                        <td>:</td>
                        <td>{{info.formula}}</td>
                    </tr>
                    <tr>
                        <td>:</td>
                        <td>{{info.applylicable}}</td>
                    </tr>
                </tbody>
            </table>
            <ul>
                <li v-for="(item,i) of pics" :key="i">
                    <img :src="''+item.pic">
                </li>
            </ul>
        <div class="bottom">
            <!--  -->
        <router-link :to="'/index'">
            <mt-button class="return home">
                <span class="mui-icon mui-icon-home"></span>
                <p></p>
                </mt-button>
         </router-link>
         <!--  -->
        <router-link :to="'/shopcart'">
            <mt-button class="return">
                <span class="mui-icon mui-icon-extra mui-icon-extra-cart"></span>
                <p></p>
                </mt-button>
         </router-link>
            <!--  -->
            <mt-button class="shopcart" @click="addcart"></mt-button>
            <mt-button class="shopping" @click="shop"></mt-button>
        </div>   
    </div>
</div>
</template>
<script>
import {MessageBox} from 'mint-ui'
import {Toast} from "mint-ui"
export default {
    data(){
        return{//lid
            lid:this.$route.query.lid,
            imgs:[],//
            info:{},//
            pics:{} //
        }
    },
    created(){
        this.foodlist();
    },
    methods:{
        // 
        shop(){
            MessageBox('', '');
            this.$router.push("/Merorder");
        },
        // 
        addcart(){
            // console.log(1111);
            // 
            var pid=this.lid;
            var pname=this.info.title;
            var price=this.info.price;
            var pic=this.info.img;
            // ajax
            var url="addcart?pid="+pid+"&price="+price+"&pname="+pname+"&pic="+pic;
            // 
            this.axios.get(url).then(result=>{
                // console.log(result.data.code);
                if(result.data.code==1){
                    Toast(",")
                    //main.js
                    //
                     this.$store.commit("increment");
                }
                
            })
        },
        // 
        foodlist(){
            //
            var url="lunbo?lid="+this.lid;
            this.axios.get(url).then(result=>{
                // console.log(result.data.data)
                this.imgs=result.data.data;
                // console.log(this.imgs);
            })
            //
            var url="comment?lid="+this.lid;
            this.axios.get(url).then(result=>{
                // console.log(result.data.data)
                 this.info=result.data.data[0];
                //  console.log(this.info);
            })
            //
            var url="foodPic?lid="+this.lid;
            this.axios.get(url).then(result=>{
            // console.log(result.data.data)
                this.pics=result.data.data;
                console.log(this.pics);
            })
        }
    }
}
</script>
<style scoped>
/**/
.btn{
background: rgba(0, 0, 0, 0.1);
    border-radius: 50%;
    position: fixed;
    top: 3px;
    left: 3px;
    z-index: 10;
    color: #fff;
}
/**/
.mint-swipe{
        height: 400px;
    }
.mint-swipe img{
        width: 100%;
    }
/***/
.mui-card{
    margin: 0;
}
.mui-card-header.mui-card-media{
    padding: 0;
}
/**/
.price{
    font-size: 20px;
    color: #d68c05;
}
.price span{
    font-size: 28px;
}
/**/
.num{
    position: absolute;
    top: 45px;
    right: 20px;
}
/*table   */
table{
    margin: 0 auto;
    border: 1px solid #c8c7cc;
    width: 98%;
    box-shadow: 0px 1px 10px 1px #d2d1d3;

}
table thead th{
    text-align: center;
    padding-bottom:5px; 
}
table tbody tr td:nth-child(2n-1){
    text-align: center
}
/**/
ul{
    padding: 0;
}
ul li{
    margin-top: -6px;
}
ul li img{
    width: 100%;
}
/**/
.bottom{
    position: fixed;
    bottom: 0;
    background: #fff;
    width: 100%;
    height: 55px;
}
/**/

.return{
    position: fixed;
    bottom: 0;
    left: 18%;
    border: 0px;
    width: 20%;
    height: 55px;
    padding: 0;
    font-size: 14px;
    background: #fff;
}
.return span{
    position: relative;
    top: 2px;
}
.return p{
    position: relative;
    top: 3px;
}
.home{
    left: 0;
}
/**/
.shopcart,.shopping{
    position: fixed;
    bottom:0;
    width: 30%;
    height: 55px;
    color: #fff;
}
.shopcart{
    left: 40%;
    background:#bed5ab;  
}
    /*  */
.shopping{
    right: 0px;
    background: #d68c05;
}
</style>



