<template>
<div class="app-shopcart">
    <!--  -->
<mt-header title=""></mt-header>
    <!--  -->
<div class="decorate"></div>
    <!--  -->
<div class="mui-card">
    <router-link to='/arr'>
    <div class="mui-card-content">
        <div class="mui-card-content-inner">
            <div class="mui-card-content-inner">
                <p>
                    <span>{{addrlist.name}}  </span>
                    <span>{{addrlist.tel}}</span>
                </p>
                <p>{{addrlist.place}}</p>
            </div>
        </div>
    </div>
    </router-link>
</div>
    <!--  -->
<ul class="mui-table-view">
    <li class="mui-table-view-cell mui-media" v-for="(item,i) of shoplist" :key=i>
        <input type="checkbox" class="check" :checked="item.cb" @click="modifyItem" :data-id="item.id" :data-i="i">
        <img class="mui-media-object mui-pull-left" :src="''+item.pic">
        <div class="mui-media-body">
            <p class="title">{{item.pname}}</p>
            <p class='mui-ellipsis'>{{item.price}}</p>
            <my-add></my-add>
            <span class="mui-icon mui-icon-trash del" :data-idx="i" :data-id="item.id" @click="delItem"></span>

        </div>
    </li>
</ul>
    <!--  -->
    <div class="order">
        <div>
        <input type="checkbox" @click="selectAll" :checked="allcb">
        </div>
        <div>
            <span class="font">:{{total.toFixed(2)}}</span>  
            <mt-button @click="mes"></mt-button></div>
        
    </div>
    <!--  -->
<nav class="mui-bar mui-bar-tab">
    <router-link class="mui-tab-item " :to="'/Index'">
        <span class="mui-icon mui-icon-home"></span>
        <span class="mui-tab-label"></span>
    </router-link>
    <router-link class="mui-tab-item" :to="'/Goodlist'">
        <span class="mui-icon mui-icon-extra mui-icon-extra-cate"></span>
        <span class="mui-tab-label"></span>
    </router-link>
    <router-link class="mui-tab-item mui-active" :to="'/shopcart'">
        <span class="mui-icon mui-icon-extra mui-icon-extra-cart">
            <span class="mui-badge">
                {{$store.getters.optCartCount}} 
            </span>
        </span>
        <span class="mui-tab-label "></span>
    </router-link>
    <router-link class="mui-tab-item" :to="'/self'">
        <span class="mui-icon mui-icon-contact"></span>
        <span class="mui-tab-label"></span>
    </router-link>
</nav>
</div>
</template>
<script>
//
import {MessageBox} from 'mint-ui'
import {Toast} from "mint-ui"
export default {
    data(){
        return{
            addrlist:[],//
            shoplist:[],//
            allcb:false,
            id:1
        }
    },
    created(){
        this.addr()
        this.loadMore()
    },
    //
    computed:{
        total(){
            var sum=0;
            for(var p of this.shoplist){
                if(this.allcb){
                sum+=p.price;
                }
            }
            return sum;
        }
    },
    methods:{
        //
        delItem(e){
            //1.id
            var id=e.target.dataset.id;
            // var id=this.id;
            console.log(id);
            var idx =e.target.dataset.idx;
            //2.url
            var url="removeItem";
            var postData=this.qs.stringify({
                id:id,
            });
            // console.log(postData);
            this.axios.post(url,postData).then(result=>{
                if(result.data.code==1){
                    this.shoplist.splice(idx,1)
                    Toast('');
                    this.$store.commit("reduce");
                }else{
                    Toast('');
                }
            })
        },
        //
        mes(){
            if(this.allcb){
                MessageBox('', '');
                this.shoplist='';
                this.$router.push("/Merorder");
            }else{
                MessageBox('', '');
            }
        },
         //
        selectAll(e){
        //
        var rs=e.target.checked;
        //
        for(var item of this.shoplist){
            item.cb=rs;
        }
        //item.cb=
        //
        this.allcb=rs;
        },
        //   
        modifyItem(e){
            //1.
            var rs=e.target.checked;
            // console.log(rs);
            //2.
            var i =e.target.dataset.i;
            // console.log(rs+":"+i)
            //3.cb
            this.shoplist[i].cb=rs;
            //
            //1.
            var size1=this.shoplist.length;
            //2.cb true 
            var count=0;
            for(var item of this.shoplist){
                if(item.cb){
                    count++;
                }
            }
            //3.==cb  true  []
            if(count==size1){
                this.allcb=true;
            }else{
                this.allcb=false;
            }
        },
        //
        addr(){
            var url="addr";
            this.axios.get(url).then(result=>{
                console.log(result.data.code);
                if(result.data.code==-1){
                    MessageBox(',');
                this.$router.push("/login");
                }else{
                this.addrlist=result.data.data[0];
                console.log(this.addrlist)
                }
            })
        },
        //
        loadMore(){
            var url="shop";
            this.axios.get(url).then(result=>{
                console.log(result.data.data);
                this.shoplist=result.data.data;
                console.log(this.shoplist);
            })
        }
    }
}
</script>
<style scoped>
/*  */
    .mui-bar-tab .mui-tab-item.mui-active{
        color: #bed5ab;
    }
    /*  */
    .mui-icon .mui-badge{
        background: #bed5ab;
        color: #fff;
    }
    /*  */
.mint-header{
    background-color:  rgba(0, 0, 0, 0.1);
    font-size: 18px;
    margin-bottom:3px;
}
    /*  */
.mui-card{
    box-shadow: 0px 1px 10px 1px #d2d1d3;    
}
    /*  */
.decorate{
    border: 50px solid #bed5ab;
    border-bottom-color: transparent;
    border-top: none;
    border-left-color: transparent;
    position: absolute;
    right: 7px;
    top: 47px;
    z-index: 10;
}
.decorate:before{
    content: '';
    position: absolute;
    top: 7px;
    right: -46px;
    font-size: 16px;
    transform: rotateZ(45deg);
    color: #fff;
}
    /*  */
.add{
    position: absolute;
    top: 25px;
    left: 60px;
}
.order{
    background: #fff;
    width: 100%;
    position: fixed;
    bottom: 50px;
    display: flex;
    flex-wrap: nowrap;
    justify-content: space-between;
    padding:0px 10px;
    align-items: center;
}
    /*  */
.title{
    color: #000;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
}
.mint-button{
    border-radius: 50px 50px 50px 50px;
    width: 150px;
    margin-left:10px;
    height: 40px ;
    background: #bed5ab;
    color: #fff;
}
.font{
    line-height: 40px;
}
    /*  */
.mui-table-view{
    margin-bottom: 45px;
}
    /*  */
.mui-table-view .mui-media-object{
    line-height: 80px;
    max-width: 80px;
    height: 80px;
}
.mui-pull-left{
    clear: both;
    position: relative;
    left: 20px;
}
    /*  */
.check{
    position: relative;
    left: -90px;
    top: 30px;
}
    /*  */
.mui-ellipsis{
    margin-top: 8px;
    margin-left: 5px;
    color: #ef4f4f;
}
.mui-media-body{
    position: relative;
    left: 5%;
}
    /*  */
.del{
    position: absolute;
    top: 45%;
    left: 80%;
}
</style>



